.wrapper {
    background-color: #f9f9f3;
}

.container {
    display: flex !important;
    align-items: center;
    flex-direction: column;
    padding-top: 136px;
    padding-bottom: 160px;
}


.users {
    margin-top: 62px;
}

.buttonContainer {
    width: 22%;
    margin-top: 55px;
}

.error {
    color: red;
    font-size: 14px;
    text-align: center;
    margin-top: 10px;
}



.item:nth-child(1),
.item:nth-child(3n + 1) {
    padding-right: 10px !important;
}

.item:nth-child(3n) {
    padding-left: 13px ;
}

.item:nth-child(1n + 4){
    margin-top: 128px;
}




@media (max-width: 945px) {
    .container {
        padding-top: 119px;
        padding-bottom: 116px;
    }

    .buttonContainer {
        width: 30%;
    }
}


@media (max-width: 600px) {
    .container {
        padding-top: 80px;
        padding-bottom: 80px;
    }

    .users {
        margin-top: 38px;
    }

    .item:not(:first-child) {
        margin-top: 41px;
    }

    .item:nth-child(1),
    .item:nth-child(3n + 1) {
        padding-right: 0 !important;
    }

    .item:nth-child(3n) {
        padding-left: 0;
    }

    .buttonContainer {
        width: 60%;
        margin-top: 30px;
    }
}