.formContainer {
    display: flex !important;
    flex-direction: column;
    align-items: center;
    padding-top: 145px;
    padding-bottom: 167px;
}

.form {
    width: 48%;
    margin-top: 16px
}

.inputContainer {
    display: flex;
    flex-direction: column;

}

.item:not(:first-child) {
    margin-top: 5px;
}


label {
    font-size: 14px;
}

.input {
    margin-top: 10px;
    padding: 10px;
    font-size: 14px;
    border: lightgray solid 1px;
    border-radius: 5px;
    outline: none;
    transition: 0.2s ease-in;
}

.input:focus {
    border-color: #137cbd ;
    box-shadow: 1px 1px 1px  lightgray;
}


.sublabel {
    font-size: 12px;
    margin-top: 5px;
    margin-bottom: 0;
}


.inputErrorText {
    color: red;
    font-size: 12px;
    margin-top: 5px;
}

.inputLabel {
    display: flex;
    justify-content: space-between;
    border: lightgray solid 1px;
    border-radius: 5px;
    transition: 0.2s ease-in;
    margin-top: 5px;

}

.fileInput{
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.inputLabelDesc {
    padding: 10px;
    flex: 11;
    color: gray;
}

.inputLabelBtn {
    flex: 2;
    border-left: lightgray solid 1px;
    padding: 10px;
    cursor: pointer;
    transition: 0.2s ease-in;
    text-align: center;
}


.inputError {
    border-color:  red;
}

.input:focus.inputError {
    border-color:  red;
}



.formControl {
    margin-top: 5px !important;
}

.formControlLabel {
    font-size: 14px;
    margin: 0;

}

.radioGroup {
    margin-top: 10px;
}

.radioBtn:not(:first-child) {
    margin-top: -3px !important;
}


.buttonContainer{
    width: 100%;
    margin-top: 17px;
   padding: 2% 27%;
}

@media (max-width: 945px) {
    .formContainer {
        padding-top: 116px;
        padding-bottom: 118px;
    }

    .form {
        width: 65%;
    }

}

@media (max-width: 480px) {
    .formContainer {
        padding-top: 80px;
        padding-bottom: 80px;
    }

    .form {
        width: 100%;
    }

    .buttonContainer{
        padding: 2% 24%;
    }
}