
.wrapper {
    display: flex;
    flex-direction: column;
    padding-top: 160px;
    padding-bottom: 156px;
}



.titleContainer {
    text-align: center;
}

.title {
    font-size: 50px;
    font-weight: 400;
    letter-spacing: -0.6px;
}

.content {
    margin-top: 65px;
}



.imgContainer {
    display: flex;
    flex-direction: column;
    align-items: center;

}

.img {
    width: 339px;
    height: 285px;
    margin-right: 5px;
}

.textContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: end;
    margin-top: -7px;
    margin-left: 12px;
}

.textContainerTitle {
    font-size: 24px;
    font-weight: 400;
    margin: 0;
    padding: 0;
}

.textContainerDescription {
    color: #4c4b4b;
    font-size: 16px;
    text-align: justify;
    line-height: 24px;
}

.textContainerDescription:nth-child(3) {
    margin-top: 10px;
}

.buttonContainer {
    margin-top: 4px;
    margin-left: -10px;
}





@media (max-width: 945px) {

    .wrapper {
        padding-top: 119px;
        padding-bottom: 116px;
    }

    .img {
        width: 247px;
        height: 207px;
    }
    .textContainer {
        padding-left: 25px;
    }

    .textContainerDescription {
        font-size: 16px !important;
    }

}

@media (max-width: 600px) {
    .wrapper {
        padding-top: 80px;
        padding-bottom: 76px;
    }

    .content {
        margin-top: 32px;
    }

    .imgContainer {
        width: 100%;
        text-align: center;
    }
    .img {
        width: 289px;
        height: 243px;
    }

    .textContainer {
        padding-left: 0;
        margin: 40px 0 0 !important;
    }

    .textContainerTitle {
        width: 100%;
        text-align: center;
    }

    .buttonContainer {
        width: 100%;
        text-align: center;
    }

}
