.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.avatar {
    width: 70px !important;
    height: 70px !important;
}




.text {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 12px;
}

.text p{
    margin: 0;
    padding: 0;
}

.name {
    text-wrap: normal;
    word-break: break-word;
    font-size: 24px;
}

.description {
    margin-top: 12px;
    color: #4c4b4b;
    line-height: 16px;
}

.description p:not(:first-child) {
    margin-top: 12px;
}

@media (min-width: 600px) and (max-width: 650px){
    .avatar {
        width: 60px !important;
        height: 60px !important;
    }
    .name {
        font-size: 20px;
    }

    .description p {
        font-size: 14px;
    }
}