

.title {
    font-size: 50px;
    font-weight: 400;
    letter-spacing: -0.6px;
    margin: 0;
    padding: 0;
}

.subtitle {
    color: gray;
    margin-top: 10px !important;
    width: 46%;
    text-align: center;
    font-size: 16px;
}


@media (max-width: 845px) {
    .title {
        font-size:  40px;
        line-height: 50px;
    }

    .subtitle {
        width: 60%;
    }

}

@media (max-width: 480px) {
    .title {
        font-size: 30px;
    }
    .subtitle {
        width: 100%;
    }
}