.wrapper {
    background-image: url(../../assets/banner-photo.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    background-position: center;
    margin-top: 42px;

}

.container {
    margin-top: 82px !important;
    margin-bottom: 130px !important;
}

.bannerText {
    margin-left: 6px;
}

.title {
    text-transform: uppercase;
    color: white;
    font-size: 50px;
    font-weight: 400;
    line-height: 54px;
    max-width: 525px;
    letter-spacing: -0.6px;
    margin-bottom: 26px;
}

.description {
    color: white;
    font-size: 16px;
    line-height: 24px;
    max-width: 535px;
}


.buttonContainer {
    margin-top: 33px;
    width: 210px;
}



@media (max-width: 845px) {
    .container {
        margin-top: 45px !important;
        margin-bottom: 70px !important;
    }

    .title {
        font-size: 40px;
        line-height: 44px;
    }

}

@media (max-width: 600px) {
    .wrapper {
        margin-top: 0 !important;
    }
    .bannerText{
        margin-top: 19%;
    }

    .title{
        font-size: 30px !important;
        line-height: 34px !important;
        text-align: center;
        padding: 0 10px;
        margin-bottom: 0;
    }

    .description{
        text-align: center;
        margin-top: 21px;
    }

    .buttonContainer {
        margin-left: auto;
        margin-right: auto;
    }

}
